<template>
  <form class="w-50 mt-5 ml-auto mr-auto pure-form pure-form-stacked flex-column" @submit.prevent="toSubmit">
    <fieldset>
      <legend>
        <h3>请求</h3>
      </legend>
      <label for="title">标题</label>
      <input class="w-100" type="text" name="title" id="title" required>
      <label for="description">描述</label>
      <textarea 
      name="description" id="description" cols="30" rows="10"
      class="w-100"
      minlength="10"
      required
      ></textarea>
      <button class="pure-button" :disabled="flag">发布</button>
    </fieldset>
  </form>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
    }
  },
  methods: {
    toSubmit(event) {
      if (this.flag) {
        return;
      }
      this.flag = true;
      const form = event.target;
      const formData = new FormData(form);
      this.$axios.post(this.$url.requests, formData)
      .then((resp) => {
        this.flag = false;
        this.$alert('success', '添加成功');
        setTimeout(() => {
          this.$router.replace('/requests')
        }, 300);
      })
      .catch(() => {
        this.flag = false;
      })
    }
  }
}
</script>

